@charset "utf-8";

body.dark ::-webkit-input-placeholder {
  /* WebKit, Blink, Edge */
  color: #fff;
}
body.dark :-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #fff;
  opacity: 1;
}
body.dark ::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #fff;
  opacity: 1;
}
body.dark :-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #fff;
}
body.dark ::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #fff;
}
@media (max-width: 1200px) {
  body.dark .osean_tm_all_wrap .leftpart {
    box-shadow:
		/* offset-x | offset-y | blur-radius | spread-radius | color */
		0px 8px 10px 1px hsla(0,0%,0%,0.14), 
		0px 3px 14px 2px hsla(0,0%,0%,0.12), 
		0px 5px 5px -3px hsla(0,0%,0%,0.2);
  }
}

body.dark ::placeholder {
  /* Most modern browsers support this now. */
  color: #fff;
}
body.dark #preloader:before,
body.dark #preloader:after {
  background-color: #fff;
}
body.dark .loader_line:before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 1px;
  height: 0%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
  background-color: #000;
  -webkit-animation: lineheight 1000ms ease-in-out 0s forwards;
  -o-animation: lineheight 1000ms ease-in-out 0s forwards;
  animation: lineheight 1000ms ease-in-out 0s forwards;
}
body.dark .loader_line:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  height: 100%;
  background-color: #ccc;
  -webkit-transform: translateY(-100%);
  -ms-transform: translateY(-100%);
  -o-transform: translateY(-100%);
  transform: translateY(-100%);
  -webkit-animation: lineround 1200ms linear 0s infinite;
  -o-animation: lineround 1200ms linear 0s infinite;
  animation: lineround 1200ms linear 0s infinite;
  animation-delay: 2000ms;
}
